.Weathercard{
    height: 220px;
    width: 320px;
    background-image: url("https://media.istockphoto.com/id/1314812112/vector/sunset-sky-cartoon-summer-sunrise-with-pink-clouds-and-sunshine-evening-cloudy-heaven.jpg?s=612x612&w=0&k=20&c=5ICXJ0K3Uu06L9OpqINVu4bUXS3UcBWJHP3Ut8MqDWU=");
    background-size: cover;
    border-radius: 20px;
    padding: 20px;

    &__header{
        display: flex;
        align-items: center;
    }

    &__location{
        font-weight: 600;
    } 

    &__main{
        height: 145px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
    }
    &__section-PVH{
        display: flex;
        flex-direction: row;
        gap: 15px;
    }

}